		// 尝试离线缓存
		offlineSave(t, item) {
			console.log('offlineSave', arguments);
			// 如果是询问是否下载
			if (t == 'ask') {
				uni.showModal({
					title: '确认缓存文件：' + item.ResourceName + '吗？',
					success(e) {
						if (e.confirm) {
							// 如果确认缓存
							console.log('开始缓存', e);
							uni.saveFile({
								tempFilePath: '/temp',
								success(r) {
									let savedFilePath = r.savedFilePath;
									uni.showToast({
										title: `文件存储在：${savedFilePath}`
									});
								}
							});
						}
					},
					complete() {}
				});
			}
		},

		// 查看离线资源
		goToOfflineResource() {
			uni.redirectTo({
				url: '/pages/students/offlineResource/offlineResource'
			});
		},
		// 返回
		back() {
			uni.redirectTo({
				url: '/pages/students/course/courseDetail/courseDetail'
			});
		},
		read(item) {
			// 如过是知网资源,直接跳转
			if (item.IsCNKI) {
				uni.navigateTo({
					url: item.Link
				});
			}
			// 如果是视频
			if (item.ResourceType == 'video'){
				uni.redirectTo({
					url:"../read/read?resType=video&resUrl=http://1256163091.vod2.myqcloud.com/7d0c14a4vodcq1256163091/aa0e30175285890795940545382/f1TJemEnRMwA.mp4"
				})
			}

			// 如果是文档阅读
			if (item.ResourceType == 'wordprocessing') {
				uni.showLoading({
					mask: true,
					title: '正在获取文件，请稍候...'
				});

				uni.downloadFile({
					header: {
						Origin: '*',
						'Access-Control-Allow-Origin': '*',
						'Access-Control-Request-Method': '*',
						'Access-Control-Request-Headers': '*',
						'Access-Control-Allow-Credentials': false
					},
					url: 'https://ve-1256163091.cos.ap-beijing.myqcloud.com/0038938a-4a43-491d-be82-e8d74633f93d.doc',
					success: function(res) {
						var filePath = res.tempFilePath;
						uni.openDocument({
							filePath: filePath,
							success: function(res) {
								console.log('打开文档成功');
							}
						});
					},
					complete: function() {
						uni.hideLoading();
					}
				});
			}

			// 如果是pdf阅读
			if (item.ResourceType == 'pdf') {
				uni.showLoading({
					mask: true,
					title: '正在获取文件，请稍候...'
				});
				uni.downloadFile({
					header: {
						Origin: '*',
						'Access-Control-Allow-Origin': '*',
						'Access-Control-Request-Method': '*',
						'Access-Control-Request-Headers': '*',
						'Access-Control-Allow-Credentials': false
					},
					url: 'https://ve-1256163091.cos.ap-beijing.myqcloud.com/000483d6-3764-4c1a-a170-f3be6a2744ac.pdf',
					success: function(res) {
						var filePath = res.tempFilePath;
						uni.openDocument({
							filePath: filePath,
							success: function(res) {
								console.log('打开文档成功');
							}
						});
					},

					complete: function() {
						uni.hideLoading();
					}
				});
			}
		},
